<template>
  <div class="registration container">
    <form>
      <fieldset class="form-group">
        <legend>1.  </legend>
        <div class="form-group row">
          <label for="email" class="col-sm-3 col-form-label text-truncate" title=" ">
            </label>
          <div class="col-sm-9">
            <input type="text" class="form-control" id="email" v-model="model.kopnik.email">
          </div>
        </div>
        <div class="form-group row">
          <label for="password" class="col-sm-3 col-form-label"></label>
          <div class="col-sm-9">
            <input type="text" class="form-control" id="password" v-model="model.kopnik.password">
          </div>
        </div>
        <div class="form-group row">
          <label for="password2" class="col-sm-3 col-form-label text-truncate" title="  "> 
            </label>
          <div class="col-sm-9">
            <input type="text" class="form-control" id="password2" v-model="model.kopnik.password2">
          </div>
        </div>
      </fieldset>

      <fieldset class="form-group">
        <legend>2.  </legend>
        <div class="form-group row">
          <label for="surname" class="col-sm-3 col-form-label"></label>
          <div class="col-sm-9">
            <input type="text" class="form-control" id="surname" v-model="model.kopnik.surname">
          </div>
        </div>
        <div class="form-group row">
          <label for="name" class="col-sm-3 col-form-label"></label>
          <div class="col-sm-9">
            <input type="text" class="form-control" id="name" v-model="model.kopnik.name">
          </div>
        </div>
        <div class="form-group row">
          <label for="patronymic" class="col-sm-3 col-form-label"></label>
          <div class="col-sm-9">
            <input type="text" class="form-control" id="patronymic" v-model="model.kopnik.patronymic">
          </div>
        </div>
        <div class="form-group row">
          <label for="birth" class="col-sm-3 col-form-label text-truncate" title=" "> </label>
          <div class="col-sm-9">
            <select class="form-control custom-select" id="birth" v-model="model.kopnik.birth">
              <option v-for="n in 100" :value="n+1900">{{n+1900}}</option>
            </select>
          </div>
        </div>
        <div class="form-group row">
          <label for="appartment" class="col-sm-3 col-form-label"></label>
          <div class="col-sm-9">
            <input type="number" class="form-control" id="appartment" min="1" v-model="model.kopnik.address.appartment">
          </div>
        </div>
      </fieldset>

      <fieldset class="form-group">
        <legend>3. </legend>
        <div class="form-group row">
          <label for="country" class="col-sm-3 col-form-label"></label>
          <div class="col-sm-9">
            <input type="text" class="form-control typeahead" id="country" autocomplete="off" v-model="model.kopnik.address.country">
          </div>
        </div>
      </fieldset>

      <fieldset class="form-group">
        <legend>4.  </legend>
        <div class="form-group row">
          <label for="fio" class="col-sm-3 col-form-label text-truncate" title="  "> 
            </label>
          <div class="col-sm-9">
            <input type="text" id="fio" class="form-control form-control-danger" placeholder="  "
                   v-model="model.fio">
            <!--<div class="form-control-feedback">  !!!</div>-->
          </div>
        </div>
        <div class="form-group row">
          <label for="phoneNumber" class="col-sm-3 col-form-label text-truncate" title=" ">
            </label>
          <div class="col-sm-9">
            <input type="text" id="phoneNumber" class="form-control"
                   placeholder="   " v-model="model.phoneNumber">
          </div>
        </div>
        <div class="form-group row">
          <label for="note" class="col-sm-3 col-form-label text-truncate" title=" ">
            </label>
          <div class="col-sm-9">
            <textarea id="note" class="form-control" style="height: 6em"
                      placeholder="       "
                      v-model="model.note"></textarea>
          </div>
        </div>
      </fieldset>

      <!-- -->
      <div class="form-group row">
        <label for="captcha" class="col-sm-3 col-form-label text-truncate" title="  "> 
          </label>
        <div class="col-sm-9">
          <div class="g-recaptcha" data-sitekey="6LfE5RIUAAAAAETHS6VcgQ4CEjdEhDYGsS4H0mVi"></div>
        </div>
      </div>

      <div class="alert alert-danger" v-if="submited && !model.isAccountReady() && !model.id">
        <h4 class="alert-heading">   </h4>
        <strong>!</strong>
        <ul class="list-unstyled2">
          <li v-if="!model.address.town">    " "</li>
          <li v-if="!model.address.street">    ""</li>
          <li v-if="!model.address.house">    ""</li>
          <li v-if="!model.captchaResponse">    </li>
        </ul>
        <strong>     "" </strong>
      </div>

      <div v-if="model.id" class="alert alert-success" role="alert">
        <h4 class="alert-heading"> </h4>
        <p>       .</p>
        <strong class="mb-0">   {{model.id}}.</strong>
      </div>

      <p>
        <button v-if="!model.id" type="submit" id="submit" @click.prevent="submit_click"
                class="btn btn-lg btn-block btn-primary">
          
        </button>
      </p>
    </form>
  </div>
</template>

<script>
  const log = require("loglevel").getLogger("registration.vue")
  import Registration from "../model/Registration"

  export default{
    data: function () {
      return {
        model: new Registration(),
        /**
         *     ""
         *       
         */
        submited: false,
        testValue: null,
      }
    },
    props: ["id"],
    components: {},
    methods: {
      submit_click(){
        this.submited = true;
        this.model.captchaResponse = grecaptcha.getResponse()
        /**
         *     
         *      
         */
        if (this.model.isRequestReady()) {
          $("#submit").attr("disabled", true)
          this.model.sendRequest()
            .then(REGISTRATION => {
              $("html, body").animate({scrollTop: $(document).height()}/*, "fast"*/);
            })
            .catch(err => {
              $("#submit").attr("disabled", false)
              throw err
            })
        }
        else {
          $("html, body").animate({scrollTop: $(document).height()}/*, "fast"*/);
        }
      },
    },
    beforeCreate(){
    },
    created: function () {
    },
    mounted: async function () {
      await this.model.auth()
      global.$("country").attr("disabled", false)
      global.$('#country').typeahead({
        source: (process)=>{

        },
        autoSelect: false,
        delay: 2000,
      })
        .change(function () {
          var current = $input.typeahead("getActive");
          if (current) {
            // Some item from your model is active!
            if (current.name == $input.val()) {
              // This means the exact match is found. Use toLowerCase() if you want case insensitive match.
            } else {
              // This means it is only a partial match, you can either add a new item
              // or take the active if you don't want new items
            }
          } else {
            // Nothing is active so it is a new value (or maybe empty value)
          }
        })

      setInterval(() => {
        try {
          this.model.captchaResponse = grecaptcha.getResponse()
        }
        catch (err) {
          this.model.captchaResponse = undefined
        }
      }, 1000)
    },
  }

</script>

<style scoped>
</style>



